import { supabase } from "@/lib/supabase";
import LandingPageWrapper from "@/components/landing/LandingPageWrapper";

export const revalidate = 0; 

export default async function LandingPage() {
  
  // 1. Fetch Products from PREMIUM stores only (Simulating Trending)
  // Logic: Get products, flatten, check store plan. 
  // Since Supabase join filtering is tricky without foreign key filters, 
  // we will fetch stores first, then products.
  
  // Get ID of premium stores
  const { data: premiumStores } = await supabase
    .from("stores")
    .select("id")
    .neq("subscription_plan", "free"); // <--- FILTER: NO FREE VENDORS

  const premiumStoreIds = premiumStores?.map(s => s.id) || [];

  // Get Products ONLY from those IDs
  const { data: products } = await supabase
    .from("products")
    .select("*")
    .in("store_id", premiumStoreIds) // <--- Only show items from paid stores
    .eq("is_active", true)
    .limit(100)
    .order("created_at", { ascending: false });

  // 2. Fetch Active Stores for the "Stores" tab (Mix is fine here, or restrict too)
  const { data: stores } = await supabase
    .from("stores")
    .select("*")
    .limit(50);

  return (
    <LandingPageWrapper 
      products={products || []} 
      stores={stores || []} 
    />
  );
}